.banner{
    height: 100vh;
    overflow: hidden;
}

.imageContainer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageContained {
    position: relative;
    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%;
}

.imageOverlay {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
}

.imageFrame-bot {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
}

.imageFrame-top {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
}

.banner .text {
    margin-top: 30vh;
    position: absolute;
    text-align: center;
    color: white;
    font-family: 'Montez', cursive;
    font-size: 5.5rem;
}

.banner .title{
    font-size: 0.7em;
    margin-bottom: 1vh;
}

.banner .date{
    margin-top: 5vh;
    font-family: 'Aleo', serif;
    font-weight: 300;
    font-size: 0.4em;
}

@media screen and (max-width: 1200px){
    .banner .text {
        font-size: 5.5em;
    }
}

@media screen and (max-width: 1024px){
    .banner .text {
        font-size: 5em;
    }
}
@media screen and (max-width: 768px){
    .banner .text {
        font-size: 3.5em;
    }
}